<template>
  <div class="mt-5 space-y-2 lg:mt-0 bg-gray-50 sm:p-4">
    <div class="border border-blue-400">
      <h1 class="my-3 text-xl font-semibold text-center">Hour/Day</h1>
      <column-chart
        adapter="chartjs"
        ytitle="Hour"
        xtitle="Date"
        :download="{ background: '#fff' }"
        :colors="['#6f5fdb', '#f4943d']"
        :data="
          dailydata.map((d) => [new Date(d.date).toLocaleDateString(), d.hour])
        "
      ></column-chart>
    </div>
    <div class="my-4 border border-red-500">
      <h1 class="my-3 text-xl font-semibold text-center">Page/Day</h1>
      <column-chart
