<template>
  <div class="container mx-2 md:mx-auto poppins">
    <div
      v-if="meetings && new Date(meetings.datetime).getTime() > Date.now()"
      class="
        flex flex-wrap
        items-center
        justify-center
        px-3
        py-5
        mt-3
        text-white
        rounded
        md:justify-between
        bg-gradient-to-l
        from-green-400
        to-green-500
      "
    >
      <div class="flex items-center">
        <span class="mr-2 material-icons"> notifications </span> You have an
        upcoming meeting on
        {{
          new Date(meetings.datetime).toDateString() +
          " " +
          new Date(meetings.datetime).toLocaleTimeString()
        }}
      </div>
      <div>
        <router-link
          to="/videomeetings"
          class="
            inline-block
            p-2
            mt-2
            text-center
            bg-black
            rounded
            shadow
            md:mt-0
          "
          >View Details</router-link
        >
      </div>
    </div>

    <div>
      <h1
        class="my-3 text-xl font-semibold text-center dark:text-white lg:hidden"
      >
        Daily Report
      </h1>
    </div>

    <div class="p-5 my-5 rounded shadow-md dark:bg-gray-900">
      <div class="my-3">
        <h2 class="mx-auto mb-2 text-lg font-bold text-center dark:text-white">
          Selected Month
        </h2>
        <input
          type="month"
          @change="filterByMonth"
          v-model="month"
          class="form-control"
        />
      </div>
      <div class="flex justify-center gap-3 mt-4">
        <button
          class="
            inline-block
            p-3
            font-semibold
            text-white
            bg-green-500
            hover:bg-green-600
          "
        >
          Apply
        </button>
      </div>
    </div>
    <div
      v-if="dailydata.length > 0"
      class="grid grid-cols-1 lg:gap-5 lg:grid-cols-2"
    >
      <div class="space-y-5">
        <div
          v-for="(d, i) in dailydata"
          :key="i"
          class="
            border border-gray-300 border-dashed
            shadow-md
            dark:text-white dark:border-gray-200
          "
        >
          <div
            class="
              flex
              items-center
              justify-between
              p-3
              bg-gradient-to-r
              dark:from-gray-700 dark:via-gray-900 dark:to-black
            "
          >
            <h2 class="text-xl font-semibold">
              {{ new Date(d.date).toDateString() }}
            </h2>
            <button
              @click="editReport(d)"
              class="
                flex
                items-center
                justify-center
                px-3
                py-3
                text-white
                transition
                duration-150
                transform
                bg-green-500
                rounded-full
                shadow-md
                hover:scale-105
              "
            >
              <span class="material-icons"> edit </span>
            </button>
          </div>

          <hr />
          <div
            class="
              flex
              items-center
              justify-between
              px-4
              py-3
              dark:text-white
              bg-gradient-to-r
              from-gray-400
              to-gray-400
              dark:from-gray-900 dark:via-gray-700 dark:to-gray-500
              report--bottom
            "
          >
            <div>
              <h2 class="text-xl font-semibold">Hours</h2>
              <h3>{{ d.hour }}</h3>
            </div>
            <div>
              <h2 class="text-xl font-semibold">Page</h2>
              <h3>{{ d.page }}</h3>
            </div>
            <div>
              <h2 class="text-xl font-semibold">Efficiency</h2>
              <h3>{{ Number(d.efficiency).toFixed(2) }}</h3>
            </div>
          </div>
        </div>
      </div>
      <div class="p-5 mt-5 shadow-sm lg:mt-0">
        <study-chart :dailydata="dailydata"></study-chart>
      </div>
    </div>

    <div v-else>
      <h2 class="text-center dark:text-white">
        No data found <br />
        Or Data will be available after 24 hour.
      </h2>
    </div>
    <Modal title="Daily report">
      <template v-slot:body>
        <vee-form
          ref="taskform"
          @submit="addDate"
          :validation-schema="reportSchema"
        >
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white" autocomplete="off"
              >Hour Studied</label
            >
            <vee-field
              type="number"
              name="hour"
              class="
                block
                w-full
                py-1.5
                px-3
                text-gray-800
                border border-gray-300
                transition
                duration-500
                focus:outline-none focus:border-black
                rounded
              "
              :placeholder="dayData.hour ? dayData.hour : 'e.g 5'"
            />
            <ErrorMessage class="text-red-600" name="hour" />
          </div>
          <div>
            <label class="inline-block mb-2 dark:text-white" autocomplete="off"
              >Page Studied</label
            >
            <vee-field
              type="number"
              name="page"
              class="
                block
                w-full
                py-1.5
                px-3
                text-gray-800
                border border-gray-300
                transition
                duration-500
                focus:outline-none focus:border-black
                rounded
              "
              :placeholder="dayData.page ? dayData.page : 'e.g 5'"
            />
            <ErrorMessage class="text-red-600" name="page" />
          </div>
          <div class="text-center">
            <button
              :disabled="loading"
              type="submit"
              class="
                px-6
                py-3
                mx-auto
                mt-3
                mb-1
                mr-1
                text-sm
                font-bold
                uppercase
                transition-all
                duration-150
                ease-linear
                bg-green-500
                border border-green-500 border-solid
                outline-none
                hover:bg-green-500 hover:text-white
                active:bg-green-600
                focus:outline-none
              "
            >
              {{ loading ? "Changing..." : "Save Changes" }}
            </button>
          </div>
        </vee-form>
      </template>
    </Modal>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import { auth } from "@/plugins/firebase";
import Modal from "@/components/Modal";
import StudyChart from "@/components/StudyChart";
export default {
  components: { StudyChart, Modal },
  async beforeRouteEnter(to, from, next) {
    const res = await fetch(
      `https://script.google.com/macros/s/AKfycbw_kdcD4HYnkByFmpIN-zUYWLeQmoqMiXcAIPc_IVtVs4h0p3NN77U18jFtwKB8TWMknw/exec?sheet=${auth.currentUser.displayName}&type=dailydata`
    );

    const data = await res.json();
    let dp = await data.routines
      .filter((r) => r.date)
      .sort((a, b) => b.id - a.id);
    let ddata = await dp.filter(
      (d) => new Date(d.date).getMonth() == new Date().getMonth()
    );

    next((vm) => {
      vm.dailydata = ddata;
      vm.alldays = dp;
    });
  },
  data() {
    return {
      charts: false,
      month: `${new Date().getFullYear()}-0${new Date().getMonth() + 1}`,
      dailydata: [],
      dayData: {
        hour: 0,
        page: 0,
      },
      submitted: false,
      loading: false,
      alldays: [],
      meetings: [],
      reportSchema: {
        hour: "required",
        page: "required",
      },
    };
  },

  methods: {
    ...mapMutations(["toggleModal"]),

    filterByMonth() {
      this.dailydata = this.alldays.filter(
        (d) => new Date(d.date).getMonth() == new Date(this.month).getMonth()
      );
    },
    editReport(d) {
      const ddata = d;
      this.dayData = ddata;
      this.toggleModal();
    },
    getData() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxZwegkJojM8GImNcevhXJpbUBiwzCFFN2sn6TICkoOTEmPl6mV_OIlaIJlfdm-HL-RYA/exec?roll=" +
          this.user.displayName
      )
        .then((res) => res.json())
        .then((data) => {
          this.meetings = data.routines.sort(
            (b, a) => b.meeting_id - a.meeting_id
          )[0];
        });
    },
    addDate(values) {
      this.loading = true;
      fetch(
        "https://script.google.com/macros/s/AKfycbw_kdcD4HYnkByFmpIN-zUYWLeQmoqMiXcAIPc_IVtVs4h0p3NN77U18jFtwKB8TWMknw/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            ...this.dayData,
            sheetid: this.user.displayName,
            ...values,
          }),
        }
      ).then(() => {
        console.log(
          JSON.stringify({
            ...this.dayData,
            sheetid: this.user.displayName,
            ...values,
          })
        );
        let index = this.dailydata.findIndex((s) => s.id == this.dayData.id);
        this.dayData = {
          ...this.dayData,
          ...values,
          hour: parseInt(values.hour),
          page: parseInt(values.page),
        };
        this.dailydata[index] = {
          ...this.dayData,
          efficiency: this.dayData.page / this.dayData.hour,
        };
        this.loading = false;
        this.toggleModal();
      });
    },
    getMeetings() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxZwegkJojM8GImNcevhXJpbUBiwzCFFN2sn6TICkoOTEmPl6mV_OIlaIJlfdm-HL-RYA/exec?roll=" +
          this.user.displayName
      )
        .then((res) => res.json())
        .then((data) => {
          this.meetings = data.routines
            .filter((k) => k.roll === this.user.displayName)
            .sort((a, b) => b.meeting_id - a.meeting_id)[0];
          console.log(data);
        });
    },
  },
  computed: {
    ...mapState(["user"]),
  },
  created() {
    this.getData();
    this.getMeetings();
  },
};
</script>

<style lang="scss" scoped>
</style>